import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from "@angular/router";
import { School } from '../../shared/school/school.model';
import { SchoolService } from '../../shared/school/school.service';
import { COMPOSITION_BUFFER_MODE } from '@angular/forms';

@Component({
  selector: 'app-school-detail',
  templateUrl: './school-detail.component.html',
  styleUrls: ['./school-detail.component.scss'],
  providers: [SchoolService]
})
export class SchoolDetailComponent implements OnInit {

  school_id;
  schoolDetail: any;
  constructor(private activatedRoute: ActivatedRoute, private schoolService: SchoolService, private router: Router) {
    this.schoolDetail = {};
    this.activatedRoute.params.subscribe((params: Params) => {
      this.school_id = params['id'];
    });
  }

  ngOnInit() {
    this.getSchool();
  }

  getSchool() {
    var school = this.schoolService.getData();
    school.snapshotChanges().subscribe(item => {
      item.forEach(element => {
        let school = element.payload.doc.data() as School
        console.log(element, this.school_id)
        if (school.school_id == this.school_id) {
          this.schoolDetail = school;
          console.log(this.schoolDetail)
        }

      });
    });
  }

  onBack() {
    this.router.navigate(['school'],
    { queryParams: { source: 'detail' } });
  }


}



