import { Component, OnInit } from '@angular/core';
import { CityService } from '../../shared/city/city.service';
import { SchoolService } from '../../shared/school/school.service';
import { SchoolTypeService } from '../../shared/type/type.service';
import { ProgramOfferedService } from '../../shared/program_offered/program_offered.service';
import { School } from '../../shared/school/school.model';
import { City } from '../../shared/city/city.model';
import { Type } from '../../shared/type/type.model';
import { Programs_offered } from '../../shared/program_offered/program_offered.model';
import { iterateListLike } from '@angular/core/src/change_detection/change_detection_util';
import { element } from 'protractor';
import { Router, ActivatedRoute, Params } from "@angular/router";


@Component({
  selector: 'app-school',
  templateUrl: './school.component.html',
  styleUrls: ['./school.component.scss'],
  providers: [CityService, SchoolService, SchoolTypeService, ProgramOfferedService]
})

export class SchoolComponent implements OnInit {
  allCityList: any[];
  allSchoolList: any[];
  allSchoolTypeList: any[];
  allProgramList: any[];
  schoolList: any[];
  isSearch = false;
  displaySchool = false;

  model = {
    searchText: '',
    selectedCity: '',
    selectedSchoolType: '',
    selectedProgram: ''
  }

  pageParItem: any = "5";
  pageNumbers: any = 1;
  totalItem: number;
  source = "";

  constructor(private cityService: CityService, private schoolService: SchoolService,
    private schooltypeService: SchoolTypeService, private programOfferedService: ProgramOfferedService, private router: Router,
    private route: ActivatedRoute) {
    this.route.queryParams.subscribe(params => {
      this.source = params['source'];
    });
  }

  ngOnInit() {
    this.getCity();
    this.getSchool();
    this.getType();
    this.getProgramOffered();
  }

  onClick(school) {
    this.router.navigate(['schooldetail', school.school_id]);
  }

  getCity() {
    var city = this.cityService.getData();
    city.snapshotChanges().subscribe(item => {
      this.allCityList = [];
      item.forEach(element => {
        let city = element.payload.doc.data() as City
        city["$key"] = element.key;
        this.allCityList.push(city);
        this.getPageCountsDisplay(this.pageParItem, this.pageNumbers, this.totalItem)
      });
    });
  }

  getSchool() {
    var school = this.schoolService.getData();
    school.snapshotChanges().subscribe(item => {
      this.allSchoolList = [];
      item.forEach(element => {
        let school = element.payload.doc.data() as School
        this.allSchoolList.push(school);
        this.getPageCountsDisplay(parseInt(this.pageParItem), this.pageNumbers, this.totalItem);
      });
      this.displaySchool = true;
      console.log(this.allSchoolList)
      if (this.source == 'detail') {
        this.onSearch();
      }
    });
  }

  getType() {
    var schoolType = this.schooltypeService.getData();
    schoolType.snapshotChanges().subscribe(item => {
      this.allSchoolTypeList = [];
      item.forEach(element => {
        let schoolType = element.payload.doc.data() as Type
        schoolType["$key"] = element.key;
        this.allSchoolTypeList.push(schoolType);

      });
    });
  }

  getProgramOffered() {
    var programOffered = this.programOfferedService.getData();
    programOffered.snapshotChanges().subscribe(item => {
      this.allProgramList = [];
      item.forEach(element => {
        let programOffered = element.payload.doc.data() as Programs_offered
        programOffered["$key"] = element.key;
        this.allProgramList.push(programOffered);
      });
    });
  }

  onSearch() {
    this.model.selectedProgram = '';
    this.model.selectedSchoolType = '';
    if ((this.model.searchText && this.model.searchText.trim() != '') || (this.model.selectedCity && this.model.selectedCity.trim() != '')) {
      this.schoolList = this.allSchoolList.filter((item) => {
        let keys = Object.keys(item);
        let valid_city = false;
        let valid_search = false;
        if (this.model.selectedCity && this.model.selectedCity.trim() != '') {
          valid_city = item.city == this.model.selectedCity.trim();
        } else {
          valid_city = true;
        }
        if (this.model.searchText && this.model.searchText.trim() != '') {
          if (!valid_search) {
            keys.forEach(key => {
              if (!valid_search && item[key] && (item[key].toLowerCase().indexOf(this.model.searchText.toLowerCase().trim()) > -1)) {
                valid_search = true;
              }
            });
          }
        } else {
          valid_search = true;
        }

        if (valid_city && valid_search) {
          return item;
        }
      })

    } else {
      this.schoolList = this.allSchoolList;
      this.getPageCountsDisplay(this.pageParItem, this.pageNumbers, this.totalItem)
    }
    this.isSearch = true;
  }

  onLeftSearch() {
    this.model.selectedCity = '';
    this.model.searchText = '';
    if ((this.model.selectedSchoolType && this.model.selectedSchoolType.trim() != '') || (this.model.selectedProgram && this.model.selectedProgram.trim() != '')) {
      this.schoolList = this.allSchoolList.filter((item) => {
        let keys = Object.keys(item);
        let valid_school_type = false;
        let valid_program_offered = false;
        if (this.model.selectedSchoolType && this.model.selectedSchoolType.trim() != '') {
          valid_school_type = item.type == this.model.selectedSchoolType.trim();
        } else {
          valid_school_type = true;
        }

        if (valid_school_type && this.model.selectedProgram && this.model.selectedProgram.trim() != '') {
          valid_program_offered = item.program_offered == this.model.selectedProgram.trim();
        } else {
          valid_program_offered = true;
        }
        if (valid_school_type && valid_program_offered) {
          return item;
        }
      })

    } else {
      this.schoolList = this.allSchoolList;
      this.getPageCountsDisplay(this.pageParItem, this.pageNumbers, this.totalItem)
    }
    this.isSearch = true;
  }

  userPageChangeEvent(pageNo) {
    this.pageNumbers = pageNo;
  }

  getPageCountsDisplay(currentPage, pageSize, totalRecords) {
    let startIndex = ((currentPage - 1) * pageSize) + 1;
    let endIndex = ((startIndex + pageSize) - 1);
    if (endIndex > totalRecords) {
      endIndex = totalRecords;
    }
    if (totalRecords == 0) {
      startIndex = endIndex = 0
    }
  }
}



